.mailList {
    width: 30vw;
    text-align: left;
    margin: 100px;
    margin-top: 30px;
    display:grid;
    grid-gap: 10px;
    overflow-y: scroll;
    height :80vh;

}


.emailItem {
    padding: 10px;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
  }
  
  .emailItem:hover {
    background-color: #f0f0f0;
  }
  
  .emailItem h4 {
    margin: 0;
    font-size: 16px;
  }
  
  .emailItem p {
    margin: 5px 0;
    font-size: 14px;
  }
  
  .emailItem:hover {
    background-color: #e0e0e0;
  }

